import { ElMessage } from "element-plus";
export default {
  name: "SearchClassSchedule",

  data() {
    return {
      // 分页
      currentPage: 1,
      pagesize: 10,
      // 搜索框
      search: "",
      // 选课的结果
      XkList: [],
      // 学年学期信息
      XnxqList: [],
      XnxqOption: "选择学期",
      Xnxqh: "",
      // 院系所信息
      YxsList: [],
      YxsOption: "选择学院",
      // 选择的院系所id
      YxsId: "",
      YxsMc: "",
      // 专业列表信息
      ZyList: [],
      ZyOption: "选择专业",
      ZyId: "",
      // 年级选择 根据班级的 入学年份
      RxnfList: [],
      RxnfOption: "选择年级",
      Rxnf: "",
      // 班级选择
      BjList: [],
      BjOption: "选择班级",
      BjId: ""
    };
  },

  created() {
    this.getXnxqList();
  },

  methods: {
    // 分页
    handleSizeChange: function (size) {
      this.pagesize = size;
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
    },

    // 获取 学期学年列表
    getXnxqList() {
      this.$axios.get("/weixin-ggjxzl/getXnxqList").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.XnxqList = data.data;
        } else {
          ElMessage.error("获取学年数据错误");
        }
      });
    },

    // 获取所有院系所信息
    getYxsList() {
      this.$axios.get("/weixin-yxs/getAllYxs").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.YxsList = data.data;
        }
      });
    },

    // 获取专业列表
    getZyListByYxsId(id) {
      this.$axios.post("/weixin-zymc/getZyList?YxsId=" + id).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.ZyList = data.data;
        }
      });
    },

    // 获取入学年份 列表
    getRxnfListByZyId(id) {
      this.$axios.post("/weixin-bjmc/getRxnfList?ZyId=" + id).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.RxnfList = data.data;
        }
      });
    },

    // 获取班级列表
    getBjListByZyId(id, rxnf) {
      this.$axios.post("/weixin-bjmc/getBjList?ZyId=" + id + "&Rxnf=" + rxnf).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.BjList = data.data;
        }
      });
    },

    // 选择学年
    changeXnxq(item) {
      this.Xnxqh = item;

      if (!this.YxsList.length) {
        this.getYxsList();
      }
    },

    // 选择院系所
    changeYxs(item) {
      if (item.dwh) {
        this.YxsOption = item.dwmc;
        this.YxsId = item.dwh;
        this.YxsMc = item.dwmc; // 修改专业列表

        this.ZyList = [];
        this.ZyOption = "选择专业";
        this.ZyId = ""; // 获取专业列表

        this.getZyListByYxsId(item.dwh);
      } else {
        ElMessage.error("请先选择学期");
      }
    },

    // 选择专业列表
    changeZy(item) {
      if (item) {
        this.ZyOption = item.zymc;
        this.ZyId = item.zyh; // 修改入学年份

        this.RxnfList = [];
        this.RxnfOption = "选择年级";
        this.Rxnf = ""; // 获取 入学年级

        this.getRxnfListByZyId(item.zyh);
      }
    },

    // 选择入学年份
    changeRxnf(item) {
      if (item) {
        this.Rxnf = item;
        this.BjList = [];
        this.BjOption = "选择班级";
        this.BjId = ""; // 根据专业号 获取班级

        this.getBjListByZyId(this.ZyId, item);
      }
    },

    // 选择班级
    changeBj(item) {
      if (item) {
        this.BjOption = item.bj;
        this.BjId = item.bh;
      }
    },

    // 查询按钮
    queryBjLabSchedule() {
      if (this.Xnxqh && this.YxsId && this.ZyId && this.BjId) {} else {
        ElMessage.error("请完成选择");
      }
    }

  },
  computed: {
    getXkFilter() {
      return this.XkList.filter(data => !this.search || data.sysmc.includes(this.search.toLowerCase()) || data.sysmph.includes(this.search.toLowerCase()));
    }

  }
};